import { useEffect, useState } from 'react'
import logo from '../assets/images/Logo.svg'
import Message from '../components/Message'
import MainScreen from '../game/MainScreen';
import GameRules from '../components/GameRules';
import Footer from '../components/Footer';


interface LandingProps {
    setLoginStart: (value: boolean) => void;
    isCodeVerified: boolean;
    handleLogout: () => void;
    phoneNumber: string;
    setActiveTab: (num: number) => void;
    activeTab: number;
}


const Landing: React.FC<LandingProps> = ({ setLoginStart, isCodeVerified, handleLogout, phoneNumber, setActiveTab, activeTab }) => {
    const [rules, showRules] = useState(false);

    return (
        <div className={`landing-container relative ${isCodeVerified ? 'verified full' : ''} `}>
            {rules && (<GameRules showRules={showRules} />)}
            <div className="landing-content container">
                {isCodeVerified ?
                    <section className="main_content">
                        <MainScreen handleLogout={handleLogout} phoneNumber={phoneNumber} activeTab={activeTab} setActiveTab={setActiveTab} />
                    </section>
                    :
                    <section className="main_content">
                        <div className="landing-logo">
                            <img src={logo} alt="" className="" />
                        </div>
                        <h1 className="landing-title">   <br />    </h1>
                        <p className="landing-text">       </p>
                        <div className="landing-buttons">
                            <button className="btn block landing-button" onClick={() => setLoginStart(true)}></button>
                            <button className="btn white block landing-button" onClick={() => setLoginStart(true)}></button>
                        </div>
                    </section>
                }
                <Footer showRules={showRules} />
            </div>
        </div>
    )
}


export default Landing
