import React, { useRef, useEffect } from 'react';
import obstacleImage from './Obstacle/obstacle.png';
import obstacleImage2 from './Obstacle/obstacle2.png';
import obstacleImage3 from './Obstacle/obstacle3.png';
import obstacleImage4 from './Obstacle/ground.png';

export const obstacleTypes = [
  { width: 80, height: 100, speed: 2, texture: obstacleImage }, // Obstacle type 1
  { width: 40, height: 40, speed: 3, texture: obstacleImage2 }, // Obstacle type 2
  { width: 60, height: 74, speed: 3, texture: obstacleImage3 }, // Obstacle type 3
  // Add more obstacle types as needed
];

const useObstacleRefs = (count) => {
  const obstacleRefs = useRef([]);

  //        count     
  const updateRefs = () => {
    obstacleRefs.current = Array.from({ length: count }, (_, index) => obstacleRefs.current[index] || React.createRef());
  };

  //     count
  useEffect(() => {
    updateRefs();
  }, [count]);

  //   
  return obstacleRefs.current;
};

export default useObstacleRefs;
